.secondary-header {
  position: relative;
  // width: 100vw;
  height: 50vh;
  background: $brand-secondary;
  color: $brand-light;
  h1 {
    padding: 1rem;
    width: 100vw;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    text-align: center;
    margin: 0 auto;
    overflow-wrap: break-word;
    text-shadow: 0 0 8px $brand-dark;
    z-index: 3;
  }
}

.secondary-header__image {
  position: absolute;
  width: 100%;
  height: 100%;
  background-position: center;
  &::after {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background: rgba(127, 127, 127, 0.2);
    background-image: radial-gradient($brand-dark 2%, transparent 80%);
    background-size: 4px 4px;
  }
}

