.main-header {
  position: relative;
  min-height: 100%;
  min-width: 100%;
  height: 100vh;
  width: 100vw;
  align-items: center;
  color: $brand-light;
  background-image: url(../img/photos/escape-room-lock.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  // background-size: 100%;
  background-attachment: fixed;

  top: 0;
  left: 0;
  background-color: $brand-dark;
  overflow: hidden;
  // z-index: 0;
  box-shadow: inset 0 0 200px black;
}

.header-overlay {
  position: absolute;
  z-index: .1;
}

.overlay-bottom {
  height: 25%;
  width: 100%;
  background: linear-gradient(transparent, $brand-dark 80%);
  bottom: 0;
}

.header--small {
  position: relative;
  height: 350px;
  background: $brand-secondary;
  text-align: center;
  h1,
  h2 {
    position: absolute;
    line-height: 250px;
    width: 100%;
    top: 100px;
    color: $brand-light;

  }
}

/*
Main header
*/

.main-header-title-container{
  position: relative;
  top: 50%;
  left: calc(12vw - 42px);
  z-index: 3;
  display: grid;
  align-content: center;
  justify-content: center;
  transform: translateY(-50%);
}

.main-header-title {
      align-self: center;
  h1 {
    color: white;
    text-shadow: 0 0 8px black;
    line-height: 0;
  }
  .main-header-breakout {
    display: block;
    font-size: responsive 36px 96px;
    font-range: 420px 1200px;
    line-height: 1em;

  }
  .main-header-augsburg {
    font-size: responsive 20px 40px;
    font-range: 420px 650px;
    line-height: .2em;
    display: inline-block;
    float: right;
   }
}